.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
	animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
	transform: rotate(0deg);
  }
  to {
	transform: rotate(360deg);
  }
}
/* TodoItem */
.TodoItems {
	font-weight: 300;
	font-size: 24px;
	margin-top: 30px;
	display: inline-block;
	width: 500px;
	border: 1px solid rgb(214, 214, 214);
	border-radius: 10px;
	box-shadow:
		0 11.3px 9.7px rgba(0, 0, 0, 0.056),
		0 30.4px 24.4px rgba(0, 0, 0, 0.08),
		0 100px 100px rgba(0, 0, 0, 0.13);
}
.TodoItem {
	display: flex;
	justify-content: space-between;
	padding: 10px 10px;
	color: #4d4d4d;
	transition: all 0.5s;
	border-bottom: 1px solid rgb(221, 221, 221);
}
.TodoItem-remove {
	margin-left: 5px;
	cursor: pointer;
}
.TodoItem input {
	font-weight: 300;
	font-size: 24px;
}
.TodoItem:hover {
	background-color: rgb(242, 242, 242);
}
.TodoItem-isDone {
	text-decoration: line-through;
	opacity: 0.5;
	transition: all 0.25s;
}
.tick {
	float: left;
	padding: 0 5px;
	color: rgb(235, 235, 235);
	cursor: pointer;
}
.tick_done {
	float: left;
	padding: 0 5px;
	color: rgb(0, 182, 36);
	cursor: pointer;
}
.header input {
	margin: 5px 10px;
	transition: all 0.5s;
	border-bottom: 1px solid white;
	opacity: 0.5;
}
.header input:focus {
	border-bottom-color: rgb(252, 15, 86);
}
.footer {
	display: flex;
	justify-content: space-between;
	font-size: 14px;
	font-weight: normal;
	padding: 10px 5px;
}
.footer-item {
	padding: 0 10px;
	cursor: pointer;
}
.footer-item-hidden {
	visibility: hidden;
}
.footer-item-active {
	border: 1px solid rgb(218, 218, 218);
	border-radius: 3px;
}
input {
    background-color: transparent;
    border: 0px solid;
    height: 20px;
    width: 160px;
	color: rgb(138, 138, 138);
}
input:focus {
	outline: none;
}